import { produce } from 'immer';
import { Props as JoyRideProps, Step } from 'react-joyride'

type NextStep = { type: 'NextStep' }
type PrevStep = { type: 'PrevStep' }
type SetStep = { type: 'SetStep', payload: number }
type Start = { type: 'Start' }
type Pause = { type: 'Pause' }
type Resume = { type: 'Resume' }
type Finish = { type: 'Finish' }
type AssignSteps = { type: 'AssignSteps', payload: Step[] }

export type ProductTourActions = Start | Pause | Resume | Finish | NextStep | PrevStep | SetStep | AssignSteps;

export interface ProductTourState extends Pick<JoyRideProps, 'steps' | 'continuous' | 'run' | 'scrollToFirstStep' | 'showSkipButton' | 'showProgress'> {
  stepIndex: number;
};

export const productTourInitialState: ProductTourState = {
  steps: [],
  stepIndex: 0,
  run: false,
  continuous: true,
  scrollToFirstStep: true,
  showProgress: true,
  showSkipButton: true,
};

export const productTourReducer = (state: ProductTourState, action: ProductTourActions): ProductTourState => {
  switch (action.type) {
    case 'Start':
      return produce(state, (draft) => {
        draft.stepIndex = 0;
        draft.run = true;
      })
    case 'Pause':
      return produce(state, (draft) => {
        draft.run = false;
      })
    case 'Resume':
      return produce(state, (draft) => {
        draft.run = true;
      })
    case 'Finish':
      return produce(state, (draft) => {
        draft.run = false;
        draft.stepIndex = 0;
      })
    case 'NextStep':
      return produce(state, (draft) => {
        draft.stepIndex = state.stepIndex + (state.run ? 1 : 0);
      })
    case 'PrevStep':
      return produce(state, (draft) => {
        draft.stepIndex = state.stepIndex - (state.run ? 1 : 0);
      })
    case 'SetStep':
      return produce(state, (draft) => {
        draft.stepIndex = action.payload;
      })
    case 'AssignSteps':
      return {
        ...state,
        steps: action.payload,
      }
    default:
      return state;
  }
};