/** @jsx jsx */
import { css, jsx } from '@emotion/core';
import React, { useCallback, useEffect } from 'react';
import Header from 'components/Header';
import styled from '@emotion/styled'
import { useHistory, useLocation, useParams } from 'react-router-dom';
import DelayedLoader from 'components/DelayedLoader';
import { Badge, Drawer, Fab, Hidden, Tab } from '@material-ui/core';
import TabPanel from '@material-ui/lab/TabPanel';
import TabContext from '@material-ui/lab/TabContext';
import TabList from '@material-ui/lab/TabList';
import { FaTasks } from 'react-icons/fa';
import Tasks from './Tasks';
import { ProductTour, productTourResumeDelay, useProductTour } from 'features/ProductTour';
import { CallBackProps } from 'react-joyride';
import AccountProfileMenu from 'components/AccountProfileMenu';
import { desktopViewPortWidth } from 'theme/variables';

const BusinessDetails = React.lazy(() => import('./account/BusinessDetails'))
const Customers = React.lazy(() => import('./account/Customers'))
const Owners = React.lazy(() => import('./account/Owners'))
const Directory = React.lazy(() => import('./account/Directory'))
const Structure = React.lazy(() => import('./account/Structure'))
const Support = React.lazy(() => import('./account/Support'))

const Title = styled.h1`
  font-size: 18px;
  margin: 0;
  padding: 16px;
  font-weight: 400;
  margin: 0 auto;
  max-width: ${desktopViewPortWidth};
`;

const TasksBadgeStyle = css`
  position: fixed;
  right: 24px;
  bottom: 24px;
`;

const TasksButtonStyle = css`
  font-size: 1.5em;
`;

const TabListContainer = styled.div`
  position: relative;

  :before {
    content: '';
    position: absolute;
    top: 44px;
    left: 0;
    right: 0;
    border-bottom: 4px solid #eee;
  }
`;

const TabPanelContainer = styled.div`
  margin: 0 auto;
  max-width: ${desktopViewPortWidth};
`;

const TabListStyle = css`
  margin: 0 auto;
  max-width: ${desktopViewPortWidth};

  .MuiTabs-indicator {
    height: 4px;
  }
`;

function Account() {
  const params = useParams();
  const history = useHistory();
  const tabFromRoute: string = (params as any).tab;
  const itemFromRoute: string = (params as any).itemId;
  const accountFromRoute: string = (params as any).accountId;
  const [activeTab, setActiveTab] = React.useState<string>(tabFromRoute);
  const [isTasksOpen, setIsTasksOpen] = React.useState(false);
  const tasksButtonRef = React.useRef<HTMLButtonElement | null>(null);
  const [{ run }, { pause, setStep, resume, assignSteps, start }] = useProductTour();
  const location = useLocation();

  const pathName = location.pathname;

  // Route Change
  React.useEffect(
    () => {
      setIsTasksOpen(false);
      if (run) {
        pause();
      }
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [pathName, pause],
  )

  const onTourChange = useCallback((data: CallBackProps) => {
    if (run) {
      // Clicked next instead of Tasks button
      if (data.action === 'next' && data.index === 1) {
        setIsTasksOpen(true);
      }
      // Clicked next instead of Insurance task
      if (data.action === 'next' && data.index === 2) {
        setIsTasksOpen(false);
        if (tabFromRoute !== 'customers') {
          history.push(`/account/${accountFromRoute}/customers`);
        }
        pause();
        setStep(3);
        setTimeout(() => {
          resume();
        }, productTourResumeDelay);
      }
      // Clicked prev from Tasks View
      if (data.action === 'prev' && data.index === 2) {
        setIsTasksOpen(false);
      }
    }
    // Clicked Insurance Task (route change triggered tour pause)
    if (!run && data.status === 'paused' && data.index === 2 && data.action === 'stop') {
      setStep(3);
      setTimeout(() => {
        resume();
      }, productTourResumeDelay);
    }
  }, [accountFromRoute, history, pause, resume, run, setStep, tabFromRoute]);


  // Mount
  useEffect(
    () => {
      assignSteps([
        {
          title: 'Welcome to APositive',
          target: 'body',
          content: "Let's get started by taking a quick look at how Tasks can streamline your application.",
          disableBeacon: true,
          disableOverlayClose: true,
          placement: 'center',
          floaterProps: {
            disableAnimation: true
          }
        },
        {
          title: 'Tasks Button',
          target: tasksButtonRef.current!, // ".MuiFab-root",
          content: "Click the button to reveal your application tasks",
          spotlightClicks: true,
          disableBeacon: true,
          disableOverlayClose: true,
          placement: 'top',
          floaterProps: {
            disableAnimation: true
          }
        },
        {
          title: 'Tasks View',
          target: ".MuiDrawer-paper",
          content: "Click on a specific task and it will take you to the required location",
          spotlightClicks: true,
          spotlightPadding: 0,
          disableBeacon: true,
          disableOverlayClose: true,
          placement: 'left',
          styles: {
            options: {
              zIndex: 10000,
              overlayColor: 'rgba(0, 0, 0, 0.01)'
            },
          },
          floaterProps: {
            disableAnimation: true
          }
        },
        {
          title: 'My first Task: Insurance Details',
          target: 'div[aria-label="InsuranceDetails"]',
          content: "Answer the question here to complete the task",
          spotlightClicks: true,
          disableBeacon: true,
          disableOverlayClose: true,
          hideFooter: true,
          placement: 'bottom',
          floaterProps: {
            disableAnimation: true
          }
        },
        {
          title: 'Congratulations!',
          target: 'body',
          content: "You've finished your first task. This completes the tour.",
          spotlightClicks: true,
          disableBeacon: true,
          hideCloseButton: true,
          disableOverlayClose: true,
          hideBackButton: true,
          placement: 'center',
          floaterProps: {
            disableAnimation: true
          }
        }
      ]);
      // start();
    },
    [assignSteps, start],
  );

  // Tab Route Change
  useEffect(
    () => {
      setActiveTab(tabFromRoute);
    },
    [tabFromRoute, setActiveTab],
  );

  function onTasksClose() {
    setIsTasksOpen(false);
  }

  function onTaskButtonClick() {
    if (run) {
      pause();
      setStep(2);
      setTimeout(() => {
        resume();
      }, productTourResumeDelay);
    }
    setIsTasksOpen(true);
  }

  function onTabClick(evt: React.MouseEvent<HTMLDivElement, MouseEvent>) {
    if (itemFromRoute) {
      history.push(`/account/${accountFromRoute}/${tabFromRoute}`);
    }
  }

  return (
    <React.Fragment>
      <Header
        profileMenu={<AccountProfileMenu />}
      />
      <Title>Breaking Bad Pty Ltd - Invoice Finance Application</Title>
      <TabListContainer>
        <TabContext value={activeTab}>
          <TabList
            css={TabListStyle}
            variant="scrollable"
            scrollButtons="auto"
            indicatorColor="primary"
            onChange={(evt, newTab) => {
              history.push(`/account/${accountFromRoute}/${newTab}`);
            }}
          >
            <Tab label="Business" value="business" />
            <Tab label="Owners" value="owners" />
            <Tab label="Customers" value="customers" />
            <Tab label="Corporate Structure" value="structure" />
            <Tab label="Directory" value="directory" onClick={onTabClick} />
            <Tab label="Xero" value="xero" />
            <Tab label="Timesheets" value="timesheets" />
            <Tab label="Supporting Information" value="support" />
            <Tab label="Offer" value="offer" />
          </TabList>
          <TabPanelContainer>
            <TabPanel value="business"><React.Suspense fallback={<DelayedLoader />}><BusinessDetails /></React.Suspense></TabPanel>
            <TabPanel value="owners"><React.Suspense fallback={<DelayedLoader />}><Owners /></React.Suspense></TabPanel>
            <TabPanel value="customers"><React.Suspense fallback={<DelayedLoader />}><Customers /></React.Suspense></TabPanel>
            <TabPanel value="structure"><React.Suspense fallback={<DelayedLoader />}><Structure /></React.Suspense></TabPanel>
            <TabPanel value="directory"><React.Suspense fallback={<DelayedLoader />}><Directory /></React.Suspense></TabPanel>
            <TabPanel value="xero"></TabPanel>
            <TabPanel value="timesheets"></TabPanel>
            <TabPanel value="support"><React.Suspense fallback={<DelayedLoader />}><Support /></React.Suspense></TabPanel>
            <TabPanel value="offer"></TabPanel>
          </TabPanelContainer>
        </TabContext>
      </TabListContainer>
      <Badge badgeContent={12} max={20} color="primary" css={TasksBadgeStyle}>
        <Fab aria-label="tasks" css={TasksButtonStyle} onClick={onTaskButtonClick} ref={tasksButtonRef}>
          <FaTasks />
        </Fab>
      </Badge>
      <Hidden mdDown>
        <Drawer
          // transitionDuration={100}
          anchor="right"
          // variant="persistent"
          open={isTasksOpen}
          onClose={onTasksClose}
        >
          <Tasks />
        </Drawer>
      </Hidden>
      <Hidden mdUp>
        <Drawer
          // transitionDuration={100}
          anchor="bottom"
          open={isTasksOpen}
          onClose={onTasksClose}
        >
          <Tasks />
        </Drawer>
      </Hidden>
      <ProductTour onTourChange={onTourChange} />
    </React.Fragment>
  )
}

export default Account;
