import React, { createContext, PropsWithChildren, useCallback, useContext, useMemo, useReducer } from 'react';
import { productTourReducer, productTourInitialState, ProductTourState } from './ProductTourReducer';
import JoyRide, { CallBackProps, Step } from 'react-joyride'

export interface ProductTourProps {
  onTourChange?: (data: CallBackProps) => void;
}

export const productTourResumeDelay = 300;

export function ProductTour({ onTourChange }: ProductTourProps) {
  const [tourState, tourActions] = useProductTour();
  const { 
    stepIndex,
    steps,
    run,
    showProgress,
    showSkipButton,
    continuous,
    scrollToFirstStep,
  } = tourState;

  const {
    finish,
    pause,
    resume,
    prev,
    next,
  } = tourActions;

  const handleJoyride = useCallback((data: CallBackProps) => {
    // console.log(data);
    if (onTourChange) {
      onTourChange(data);
    }

    if (data.action === 'close' || data.status === 'finished' || data.status === 'skipped') {
      finish();
    } else if (run && data.type === 'step:after') {
      if (data.action === 'next') {
        next();
        pause();
        if (stepIndex !== data.size - 1) {
          setTimeout(() => {
            resume();
          }, productTourResumeDelay);
        }
      } else if (data.action === 'prev') {
        prev();
        pause();
        setTimeout(() => {
          resume();
        }, productTourResumeDelay);
      }
    }
  }, [finish, next, onTourChange, pause, prev, resume, run, stepIndex]);

  return (
    <JoyRide
      run={run}
      stepIndex={stepIndex}
      steps={steps}
      continuous={continuous}
      scrollToFirstStep={scrollToFirstStep}
      showProgress={showProgress}
      showSkipButton={showSkipButton}
      callback={handleJoyride}
    />
  )
}

export interface ProductTourProviderProps { }

export interface ProductTourMethods {
  resume: () => void;
  pause: () => void;
  setStep: (index: number) => void;
  start: () => void;
  next: () => void;
  prev: () => void;
  finish: () => void;
  assignSteps: (steps: Step[]) => void;
}

// export const ProductTourContext = createContext<[ProductTourState, Dispatch<ProductTourActions>] | undefined>(undefined);
export const ProductTourContext = createContext<[ProductTourState, ProductTourMethods] | undefined>(undefined);
export const useProductTour = () => useContext(ProductTourContext)!;

function ProductTourProvider({ children }: PropsWithChildren<ProductTourProviderProps>) {
  const [state, dispatch] = useReducer(productTourReducer, productTourInitialState)

  // is there a better structure to prevent re-renders?
  const resume = useCallback(() => {
    dispatch({ type: 'Resume'});
  }, [dispatch])

  const pause = useCallback(() => {
    dispatch({ type: 'Pause'});
  }, [dispatch])

  const setStep = useCallback((index: number) => {
    dispatch({ type: 'SetStep', payload: index });
  }, [dispatch])

  const start = useCallback(() => {
    dispatch({ type: 'Start'});
  }, [dispatch])

  const next = useCallback(() => {
    dispatch({ type: 'NextStep'});
  }, [dispatch])

  const prev = useCallback(() => {
    dispatch({ type: 'PrevStep'});
  }, [dispatch])

  const finish = useCallback(() => {
    dispatch({ type: 'Finish'});
  }, [dispatch])

  const assignSteps = useCallback((steps: Step[]) => {
    dispatch({ type: 'AssignSteps', payload: steps });
  }, [dispatch])

  // is there a better structure to prevent re-renders?
  const actions = useMemo(() => (
    { resume, pause, start, finish, assignSteps, setStep, next, prev }
  ), [resume, pause, start, finish, assignSteps, setStep, next, prev]);

  return (
    <ProductTourContext.Provider
      value={[state, actions]}
    >
      {children}
    </ProductTourContext.Provider>
  )
}

export { ProductTourProvider }
